@use '../../partials/vairables' as *;
@use '../../partials/mixins' as *;
@use '../../partials/globals';

.hero{
	overflow: hidden;
	&__container{
		pointer-events: none;
		margin: 0;
		height: 28rem;
		background-color: rgb(0,0,0,0.25);
		box-sizing: border-box;
		overflow: hidden;
		@include tablet{
			height: 35rem;
		}
		@include desktop{
			height: 50rem;
		}
	}
	&__img{
		cursor: pointer;
		pointer-events: all;
		width: 100vw;
		height: 28rem;
		z-index: -2;
		display: flex;
		object-fit: cover;
		@include tablet{
			height: 35rem;
			max-width: 100%;
		}
		@include desktop{
			max-width: 1280px;
			height: 50rem;
			margin: 0 auto;
		}
	}

	&__category-container{ 
		position: absolute;
		width: 100vw;
		top: 7rem;
		pointer-events: none;
		display: flex;
		justify-content: space-around;
		align-items: center;
		z-index:0;
		@include tablet{
			top: 10rem;
		}
		@include desktop{
			justify-content: center;
		}
	}
	&__category{
		pointer-events: all;
		text-decoration: none;
		height: 2rem;
		width: 20%;
		background-image: url('../../assets/electronics.jpeg');
		background-size: cover;
		background-position: center;
		font-weight: 600;
		color: $white;
		text-align: center;
		padding: 0.5rem 0;
		border: none;
		border-radius: 0.5rem;
		@include tablet{
			background-size: cover;
			height: 3rem;
			max-width: 12rem;
			padding: 1rem;
		}
		@include desktop{
			margin: 0 1rem;
		}
		&--one{
			background-image: url('../../assets/clothing.jpeg');
			background-size: cover;
		}
		&--two{
			background-image: url('../../assets/general.jpeg');
			background-size: cover;
		}
		&--three{
			background-image: url('../../assets/plants.jpeg');
			background-size: cover;
			background-position: center -1rem;
		}

	}
	&__controls{
		position: absolute;
		z-index: 2;
		top: 15rem;
		pointer-events: none;
		flex-grow: 1;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		height: 3rem;
		width: 100%;
		pointer-events: none;
		@include tablet{
			top: 25rem;
		}
		@include desktop{
			top: 30rem;
		}
		@include desktop-break{
			justify-content: center;
		}
	}
	&__next{
		pointer-events: all;
		cursor: pointer;
		color: $mint;
		font-size: 3rem;
		@include desktop-break{
			margin-left: 1440px;
		}
		&:hover{
			color: $dark-navy;
		}
		&--hidden{
			visibility: hidden;
			pointer-events: none;
		}
	}
	&__prev{
		cursor: pointer;
		color: $mint;
		pointer-events: all;
		font-size: 3rem;
		&:hover{
			color: $dark-navy;
		}
		&--hidden{
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
		}
	}
	&__text-container{
		pointer-events: none;
		position:absolute;
		width: 100%;
		top: 25rem;
		padding: 0 1rem;
		z-index: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom:1.25rem;
		font-size: 2rem;
		@include tablet{
			top: 32rem;
		}
		@include desktop{
			top: 50rem;
		}
	}

	&__title{
		color: $white;
		margin: 0;
	}

	&__price{
		color: $white;
		margin: 0;
	}
}

