@use './vairables' as *;
@use './mixins' as *;
html{
	box-sizing: border-box;
	font-size: 100%;
	height:100%;
	width:100%;
	box-sizing: border-box;
}
*{
	box-sizing: inherit;
}
body{
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}
a{
	text-decoration: none;
	cursor: pointer;
}
button{
	cursor: pointer;
}
